import req from './req'
import axios from 'axios'
import S from '@/spx'
export function getOssToken (params) {
  return req.post('/espier/oss_upload_token', {
    filetype: 'image',
    ...params
  })
}

export function localUpload (tokenRes, file, filetype = 'image') {
  const formData = new FormData()
  formData.append('key', tokenRes.key)
  formData.append('domain', tokenRes.domain)
  formData.append('images', file)
  formData.append('filetype', filetype)
  formData.append('isUploadFile', true)
  let baseUrl = window.__POWERED_BY_QIANKUN__ ? S['SYS_CONFIG'].baseUrl : process.env.VUE_APP_API_BASE_URL
  let headers = {};
  if (window.__POWERED_BY_QIANKUN__) {
    headers['Authorization'] = 'bearer ' + S['SYS_CONFIG'].token
  } else {
    headers['Authorization'] = 'bearer ' + process.env.VUE_APP_TOKEN
  }
  return axios({
    method: 'POST',
    url: baseUrl + '/espier/upload_localimage',
    headers: { 'Content-Type': 'multipart/form-data;charset=UTF-8', ...headers },
    data: formData
  })
}

export function aliUpload (tokenRes, file) {
  const formData = new FormData()
  formData.append('key', tokenRes.dir)
  formData.append('policy', tokenRes.policy)
  formData.append('OSSAccessKeyId', tokenRes.accessid)
  formData.append('success_action_status', '200')
  // formData.append('callback', tokenRes.callback)
  formData.append('signature', tokenRes.signature)
  formData.append('name', file.name)
  formData.append('file', file)
  return axios({
    method: 'POST',
    url: tokenRes.host,
    headers: { 'Content-Type': 'multipart/form-data;charset=UTF-8' },
    data: formData
  })
}
